import React,{useEffect,useState} from 'react'
import './css/index.css'
import main from './js/snowfall.jquery.js'
import $ from 'jquery'
import FluidWords from '../FluidWords'
import Garden from '../Garden'
export default function index(props) {
    const [showWords, setshowWords] = useState(true)
    useEffect(() => {
        main();
        $(document).snowfall({
            flakeCount: 30  //爱心的个数
        });
    }, [])
    return (
        <div id="love-rain">
           {showWords? <FluidWords />:<Garden></Garden>}
            <div className='switch' onClick={()=>{setshowWords(!showWords)}}>switch</div>
            <div className='switch private' onClick={()=>{props.history.push('/love/login')}}>private</div>
		</div>
    )
}
