import React,{useEffect} from 'react'
import './index.css'
import main from './main.js'
export default function index() {
    useEffect(() => {
        main()
        function IsPC() {
            var userAgentInfo = navigator.userAgent;
            var Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod");
            var flag = true;
            for (var v = 0; v < Agents.length; v++) {
                if (userAgentInfo.indexOf(Agents[v]) > 0) {
                    flag = false;
                    break;
                }
            }
            return flag;
        }
        if(!IsPC()){
            const loveHeartDom = document.getElementById('pinkboard');
            const loverRect = loveHeartDom.getBoundingClientRect();
            const loverDomWith = loverRect.width;
            const viewWith = document.body.clientWidth;
            const loverDomLeft = loverRect.left;
            const marginLeft = loverDomLeft+loverDomWith/2 - viewWith/2;

            // const left = (loverDomWith-viewWith)/2;
            // const proportion  = viewWith/loverDomWith;
            // const loughProportion = proportion.toFixed(2)
            loveHeartDom.style.marginLeft=-marginLeft +'px';
            // loveHeartDom.style.transform = `scale(${0.7})`
    
        }
    }, [])
    return (
        <div id='waveHeart'>
<canvas id="pinkboard"></canvas>
            
        </div>
    )
}
