import React,{useEffect,useState} from 'react'
import './css/style.css'
import {main} from './js/qixi.js'
/**
 * 强制刷新
 */

  
export default function index(props) {
	const [num, setNum] = useState(0);
	const [flag, setFlag] = useState(true)

    useEffect(() => {
		const timer=setTimeout(() => {
			setNum(num+1)
		}, 1000);
		if(flag){
			main();  
			setFlag(false)
		}
		return ()=>{
			clearTimeout(timer)
		}
    }, [num])
    return (
        <div id="content">
			<ul className="content_wrap">
				<li>
					<div className="a_background">
						<div className="a_background_top"></div>
						<div className="a_background_middle"></div>
						<div className="a_background_botton"></div>
					</div>
	                   <div className="cloudArea">
	                    <div className="cloud cloud1 cloud1Anim"></div>
	                    <div className="cloud cloud2 cloud2Anim"></div>
	                </div>
	                <div id="sun"></div>
				</li>
				<li>
                	<div className="b_background"></div>
                	<div className="b_background_preload"></div>
               		<div className="shop">
                    	<div className="door">
	                        <div className="door-left"></div>
	                        <div className="door-right"></div>
                  		</div>
	                    <div className="lamp"></div>
               		</div>
               		<div className="bird"></div>
				</li>
				<li>
	                <div className="c_background">
	                    <div className="c_background_top"></div>
	                    <div className="c_background_middle"></div>
	                    <div className="c_background_botton"></div>
	                </div>
	                <div className="girl"></div>
	                <div className="bridge-bottom">
	                    <div className="water">
	                        <div id="water1" className="water_1"></div>
	                        <div id="water2" className="water_2"></div>
	                        <div id="water3" className="water_3"></div>
	                        <div id="water4" className="water_4"></div>
	                    </div>
	                </div>
	                <ul className="stars">
	                    <li className="stars1"></li>
	                    <li className="stars2"></li>
	                    <li className="stars3"></li>
	                    <li className="stars4"></li>
	                    <li className="stars5"></li>
	                    <li className="stars6"></li>
	                </ul>
	                <div className="logo"></div> 
				</li>
			</ul>
			<div id="snowflake"></div>
			<div id="boy" className="charector"></div>
			{
			num>30?(<div id='toRender' onClick={()=>{
				props.history.push('/love/loveRain')
			}}>GO ON!</div>):null
			}
			
	</div>
    )
}
