import React,{useState} from 'react'
import './index.css'
import { Transition } from 'react-transition-group';

export default function index(props) {
    const [showCard, setshowCard] = useState(false);
    const [showHome, setshowHome] = useState(false)
    const password ='0506';
    const inputRef =React.createRef();
    return (
        <div className="wrapper flex_box">
            <form>
                <div className="input">
                    <label style={{fontWeight:'bolder'}}>password:</label>
                    <input ref={inputRef} className="name" placeholder="Please enter your password" type="password" />
                </div>

                <div className="button_wrapper">
                    <button onClick={(e)=>{
                        e.stopPropagation();
                        e.preventDefault()
                        if(inputRef.current.value!==password){
                            setshowCard(true)
                        }else{
                            setshowHome(true)
                        }
                    }}>confirm!</button>
                </div>
                {
                    showHome&&<div className="button_wrapper">
                    <button onClick={(e)=>{
                        e.stopPropagation();
                        e.preventDefault()
                        props.history.push('/love/navigation')
                    }}>Go Navigation</button>
                </div>
                }
                
            </form>
            <Transition in={showCard} timeout={1000}
            >
                {state => {
                    // console.log(state)
            //state 为 entered entering exited exiting
                    return <div className={`model ${state}`} onClick={()=>{setshowCard(false)}}>
                    <div>The password is incorrect. You need to enter the correct password.Please touch anywhere to close it</div>
            </div>
                }}

            </Transition>
           
        </div>
    )
}
