import React ,{useEffect} from 'react'
import bcVideo from '../../assets/bc-video1.mp4'
import './index.css'
import WaveHeart from '../WaveHeart'
import InputPassword from '../InputPasswrod'
import { exitFull } from '../../utils/isFullScreen'
export default function index(props) {
    const wrapper = React.createRef();
    const video = React.createRef();
    useEffect(() => {
        const rect =wrapper.current.getBoundingClientRect();
        const wrapperWidth = rect.width;
        const wrapperheight = rect.height;
        const rectVideo = video.current.getBoundingClientRect();
        const videoWidth = rectVideo.width;
        const videoHeight = rectVideo.height;
        const scaleWidth = videoWidth/videoHeight - wrapperWidth/wrapperheight;
        if(scaleWidth<0){
            video.current.style.width=wrapperWidth+'px'
        }else{
            video.current.style.height=wrapperheight+'px'
        }
    }, [])
    return (
        <div ref={wrapper} id='videoBackground'>
            <video ref={video} id='video' src={bcVideo} loop autoPlay muted>
                您的浏览器不支持 video 标签。
            </video>
            <div className='container'>
                <WaveHeart></WaveHeart>
                <InputPassword {...props}></InputPassword>
            <div className='switch private' style={{
                bottom:80
            }} onClick={()=>{props.history.push('/love')}}>HOME</div>

            <div className='exitFullScreen' style={{
                bottom:30,
                fontSize:12
            }} onClick={()=>{exitFull()}}>Exit full</div>

            </div>
        </div>
    )
}
