
import React, {  useEffect } from 'react'



import './css/default.css'
import mian from './js/main.js'

export default function index() {
    useEffect(() => {
        mian();
        //判断是否是pc端 true为pc端 false为移动端
    function IsPC() {
        var userAgentInfo = navigator.userAgent;
        var Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod");
        var flag = true;
        for (var v = 0; v < Agents.length; v++) {
            if (userAgentInfo.indexOf(Agents[v]) > 0) {
                flag = false;
                break;
            }
        }
        return flag;
    }
    if(!IsPC()){
        const loveHeartDom = document.getElementById('loveHeart');
        const loverDomWith = loveHeartDom.getBoundingClientRect().width;
        const viewWith = document.body.clientWidth;
        const left = (loverDomWith-viewWith)/2;
        const proportion  = viewWith/loverDomWith;
        const loughProportion = proportion.toFixed(2)
        loveHeartDom.style.left=-left +'px';
        loveHeartDom.style.transform = `scale(${loughProportion})`

    }
 
    }, [])
    return (
    
		
		<div id="loveHeart">
			<canvas id="garden"></canvas>
			<div id="words">
				<div id="messages">
					Qian, I have fallen in love with you for
					<div id="elapseClock"></div>
				</div>
				<div id="loveu">
					Love u forever and ever.<br/>
					<div className="signature">- YuYu | China</div>
				</div>
			</div>
		</div>

    )
}
